<template>
    <transition name="modal">
        <div class="modal-mask">
            <div class="modal-wrapper"@click.self="$parent.$parent.showAddDeckModal=false">
                <div class="modal-container">
                    <v-card class="teal darken-4">
                        <v-card-text>
                            <v-container fluid>
                                <v-row>
                                    <v-col xs12>
                                        <h3 class="white--text">Add your own Deck to your Profile</h3>
                                    </v-col>
                                </v-row>
                                <v-row>
                                    <v-col xs8 offset-xs2>
                                        <v-text-field label="Title" v-model="title" dark></v-text-field>
                                    </v-col>
                                </v-row>
                                <v-row>
                                    <v-col xs8 offset-xs2>
                                        <v-text-field label="Image Url" v-model="imageUrl" dark></v-text-field>
                                    </v-col>
                                </v-row>
                                <v-row>
                                    <v-col xs8 offset-xs2>
                                        <v-text-field label="Quick Image Description" v-model="imageDesc" dark></v-text-field>
                                    </v-col>
                                </v-row>
                                <v-row>
                                    <v-col xs8 offset-xs2>
                                        <v-text-field label="Description" v-model="description" dark></v-text-field>
                                    </v-col>
                                </v-row>
                                <v-row>
                                    <v-col xs2 offset-xs5>
                                        <v-checkbox v-model="private" value="left" label="Private" dark />
                                    </v-col>
                                </v-row>
                                <v-row>
                                    <v-col xs4 offset-xs4 @click="addDeck">
                                        <a class="white--text">Done</a>
                                    </v-col>
                                </v-row>
                            </v-container>
                        </v-card-text>
                    </v-card>
                </div>
            </div>
        </div>
    </transition>
</template>

<script>
    export default {
        name: 'AddDeck',
        data() {
            return {
                title: '',
                imageUrl: '',
                imageDesc: '',
                description: '',
                private: false,
            }
        },
        methods: {
            addDeck() {
                var body = {
                    title: this.title,
                    description: this.description,
                    imageUrl: this.imageUrl,
                    imageDesc: this.imageDesc,
                    userId: this.$root.store.state.user._id,
                    author: this.$root.store.state.user.userName,
                    private: this.private
                }
                this.$root.store.actions.addDeck(body)
                this.$parent.$parent.showAddDeckModal = false
            }
        }
    }

</script>

<style scoped>
    .modal-mask {
        position: fixed;
        z-index: 9998;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background-color: rgba(0, 0, 0, .5);
        display: table;
        transition: opacity .3s ease;
    }
    
    .modal-wrapper {
        display: table-cell;
        vertical-align: middle;
    }
    
    .modal-container {
        width: 75%;
        margin: 0px auto;
        padding: 20px 30px;
        background-color: #fff;
        border-radius: 15px;
        box-shadow: 0 2px 8px rgba(0, 0, 0, .33);
        transition: all .3s ease;
        font-family: Helvetica, Arial, sans-serif;
    }
    
    .modal-header h3 {
        margin-top: 0;
        color: #42b983;
    }
    
    .modal-body {
        margin: 20px 0;
    }
    
    .modal-default-button {
        float: right;
    }
    
    .modal-enter {
        opacity: 0;
    }
    
    .modal-leave-active {
        opacity: 0;
    }
    
    .modal-enter .modal-container,
    .modal-leave-active .modal-container {
        -webkit-transform: scale(1.1);
        transform: scale(1.1);
    }
</style>


